// import { useState } from "react"

const Productinfo = ({product, addProduct}) => {
    return (
        <tr className="products">
            <td><img className="productimg" src={product.img} alt="product"/></td>
            <td>
                <p>{product.name}</p>
                <p>{product.price}€</p>
                <p>{product.stock-product.amount} in stock</p>
                {/* <p>amount: {product.amount}</p> */}
                <button onClick={()=>addProduct(product.id)}>add to cart</button>
            </td>
        </tr>
    )
}

const Productlist = ({products, setProducts}) => {
    // const [products, setProducts] = useState(productlist);

    const addProduct = id => {
        setProducts(products.map(product => {
            if(id === product.id && 1 + product.amount <= product.stock){
                product = {...product, amount: product.amount + 1};
                // product.amount = product.amount + 1;
                // console.log(product.amount);
            }
            return product;
        }))
    }

    return (
        <table>
            <tbody>
            {products.map(s=><Productinfo key={s.id} product={s} addProduct={addProduct}/>)}
            </tbody>
        </table>
    )
}


export {Productlist};