import {useState} from 'react';
import './App.css';
import {products} from './components/products-data.js';
import {Productlist} from './components/products';
import Basketlist from './components/Basket';

const App = () => {
  const [productsdata, setProducts] = useState(products);
  // console.log(productsdata)
  return (
    <div className="App">
      <header className="App-header">
        fanikauppa
      </header>
      <Basketlist basketlist = {productsdata} setProducts = {setProducts}/>
      <Productlist products = {productsdata} setProducts = {setProducts}/>
    </div>
  );
}

export default App; 