import { useState } from "react";

const Basket = ({product, addProduct}) => {
    return (
        <div>
            {product.name} <b>x{product.amount}</b> 
            <button onClick={()=>addProduct(product.id, 1)}>+</button>/
            <button onClick={()=>addProduct(product.id, -1)}>-</button>
            {/* <button onClick={()=>addProduct(product.id, -product.amount)}>x</button> */}
            {/* <input type="number" min="1" max={product.stock} value={product.amount}/> */}
        </div>
    )
}

const ConfirmForm = ({submitHandler}) => {
    const [newName, setName] = useState("");
    const [newEmail, setEmail] = useState("");
    const [newPhone, setPhone] = useState("");
    return (
        <form onSubmit={e => submitHandler(e, newName, newEmail, newPhone)}>
            name:
            <input onChange={e=>setName(e.target.value)} type="text" value={newName} required/><br/>
            email:
            <input onChange={e=>setEmail(e.target.value)} type="text" value={newEmail} required/><br/>
            phonenumber:
            <input onChange={e=>setPhone(e.target.value)} type="text" value={newPhone} required/><br/>
            <input type="submit"/>
            
        </form>
    )
}
const Summary = ({detail, products, confirmOrder}) => {
    // console.log("detail",detail)a
    return (
        <div>
            products: <br/>
            {products.map(s=>{
                if(s.amount > 0){
                    return <div key={s.id}>{s.name} x{s.amount}</div>
                }else{
                    return null
                }
            })} <br/>
            name: 
            {detail.name} <br/>
            email: 
            {detail.email} <br/>
            phonenumber: 
            {detail.phone} <br/>
            <button onClick={()=>confirmOrder()}>confirm order</button>
        </div>
    )
}

const Basketlist = ({basketlist, setProducts}) => {
    const [show, setShow] = useState(false);
    const [showConfirm, setShowConfirm] = useState(false);
    const [showSummary, setShowSummary] = useState(false);
    const [details, setDetails] = useState({});

    const reloadPage = () => {
        window.location.reload(false)
    }

    const submitHandler = (e, name, email, phone, address) => {
        e.preventDefault();
        setDetails({name: name,
                    email: email,
                    phone: phone, 
                    address: address});
        setShowSummary(!showSummary);
    }
    // const [price, setPrice] = useState(0);

    const addProduct = (id, amount) => {
        setProducts(basketlist.map(product => {
            if(id === product.id && amount + product.amount <= product.stock){
                product = {...product, amount: product.amount + amount};
            }
            return product;
        }))
    }
    const emptyBasket = () => {
        setProducts(basketlist.map(product => {
            product = {...product, amount: product.amount = 0};
            return product;
        }))
    }
    const confirmOrder = () => {
        // emptyBasket();
        // setShowConfirm(false);
        // setShowSummary(false);
        reloadPage();
    }


    let price = 0;
    return (
        <div className="basket">
            <button onClick={()=>setShow(!show)}>
            {show ? "Hide basket" : "Show basket"} </button>
            {show &&
                <div>Basket:
                    <button onClick={()=>emptyBasket()}>empty</button>

                    {basketlist.map(s=>{
                        if(s.amount > 0){
                            price += s.price*s.amount
                            return <Basket key={s.id} product={s} addProduct={addProduct}/>
                        }else{
                            return null
                        }
                    })}
                    <hr/>
                    <div>= <i>{price}€</i></div>

                    {price > 0 && 
                        <button onClick={()=>setShowConfirm(!showConfirm)}>
                        {showConfirm ? "return" : "go to checkout"} </button>
                    }
                    {showConfirm &&
                    <div>
                        {showSummary ?
                            <Summary detail={details} products={basketlist} confirmOrder={confirmOrder}/> :
                            <ConfirmForm submitHandler={submitHandler}/>
                        }
                    </div>
                    }
                    
                </div>
            }
        </div>
    )
}

export default Basketlist;